 .form-container {
    margin: 14vh auto;
    background: url(/Chatbook/static/media/login-page.94898fd4.png);
    width: 80vw;
    box-shadow: 0 0.6em 1em 0 rgba(0, 0, 0, 0.2), 0 0.8em 2.5em 0 rgba(0, 0, 0, 0.2);
    position: relative;
    border-radius: 1.2em;
    height: 61vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .form-content-left {
    background: linear-gradient(
      90deg,
      rgb(39, 176, 255) 0%,
      rgb(0, 232, 236) 100%
    );
    border-radius: 1.25em 0 0 1.25em;
    position: relative;
  }

  .form-content-right {
    border-radius: 1.25em 0 0 1.25em;
    position: relative;
    background: linear-gradient(90deg, rgb(40, 40, 40) 0%, rgb(17, 17, 17) 100%);
  }
  
  .form {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
    
  h1.form {
    font-size: 2.4em;
    text-align: start;
    width: 80%;
    margin-bottom: 1em;
    color: #fff;
  }
  
  .form-inputs {
    margin-bottom: 0.5em;
    width: 80%;
  }
  
  .form-inputs p {
    font-size: 0.8rem;
    margin-top: 0.5rem;
    color: #f00e0e;
  }
  
  .form-label {
    display: inline-block;
    font-size: 2.2em;
    font-weight: 600;
    margin-bottom: .4em;
    color: #fff;
  }
  
  .form-input {
    display: block;
    padding-left: 1.25em;
    outline: none;
    border-radius: .25em;
    height: 5em;
    width: 100%;
    border: none;
  }
  
  .form-input::-webkit-input-placeholder {
    color: #595959;
    font-size: 1.5em;
  }
  
  .form-input:-ms-input-placeholder {
    color: #595959;
    font-size: 1.5em;
  }
  
  .form-input::placeholder {
    color: #595959;
    font-size: 1.5em;
  }
  
  .form-input-btn {
    width: 80%;
    height: 6.25em;
    margin-top: 1.25em;
    border-radius: .25em;
    background: linear-gradient(
      90deg,
      rgb(39, 176, 255) 0%,
      rgb(0, 232, 236) 100%
    );
    outline: none;
    border: none;
    color: #fff;
    font-size: 1.9em;
  }
  
  .form-input-btn:hover {
    cursor: pointer;
    background: linear-gradient(
      90deg,
      rgb(39, 143, 255) 0%,
      rgb(12, 99, 250) 100%
    );
    transition: all 0.4s ease-out;
  }
  
  .form-input-login {
    font-size: 0.8em;
    margin-top: 1.25em;
    color: #fff;
    width: 80%;
    text-align: center;
  }
  
  .form-input-login a {
    text-decoration: none;
    color: #27cdff;
    font-weight: 600;
  } 

  .close-btn {
    position: absolute;
    top: 2%;
    right: 3%;
    font-size: 1.5em;
    z-index: 1;
    color: #fff;
    cursor: pointer;
  }
  
  
  .form-success {
    text-align: center;
    font-size: 3em;
    margin-top: 10em;
    color: #fff;
  }
  
